<template>
  <v-container>
    <template>
      Total Inicial de clientes: {{ totalInit }}<v-divider></v-divider
    ></template>

    <template>
      <div v-for="(node, index) in simulations" :key="node.name">
        <div style="mt-4">
          <b>
            <v-icon :color="node.isAdd ? 'green' : 'red'">{{
              node.isAdd ? "mdi-plus-circle" : "mdi-close-circle"
            }}</v-icon
            >{{ node.name }}:</b
          >
          <p>{{ node.value }}</p>
        </div>
        <template>
          Total: <b>{{ node.total }}</b> <v-divider></v-divider
        ></template>
      </div>
    </template>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      simulations: [],
      totalInit: 0,
      selection: [],
    };
  },
  methods: {
    getValueFromDataTable(dataTable, fieldName) {
      let col = dataTable._columns.find((col) => {
        if (col._fieldName.includes("(")) {
          let _fieldName = col._fieldName.substring(
            col._fieldName.indexOf("(") + 1,
            col._fieldName.length - 1
          );
          return _fieldName == fieldName;
        }
        return col._fieldName == fieldName;
      });
      if (typeof col != "undefined") {
        return dataTable._data[0][col._index]._value;
      } else {
        return null;
      }
    },
  },
  async mounted() {
    let ref = this;

    tableau.extensions.initializeDialogAsync().then(function (openPayload) {
      let dataTable = JSON.parse(openPayload);
      console.log(dataTable);
      ref.totalInit = dataTable.totalInit;
      ref.selection = dataTable.selection;
      ref.simulations = dataTable.simulations;
    });
  },
};
</script>

<style scoped></style>
